<template>
    <div id="Title">
        <img src="../../assets/images/FanHui.svg" class="photo" :style="'visibility:'+back+';'" id="back" @click="backf()">
        <p>{{title}}</p>
        <img src="../../assets/images/fatie.svg" class="photo" :style="'visibility:'+write+';'+'display:'+writeDisplay+';'" id="write" @click="to('Posts/writePost')">
        <img src="../../assets/images/shanchu.svg" class="photo" :style="'display:'+cutDisplay+';'" id="cut" @click="change('Postdetails','Change')">
        <img src="../../assets/images/wancheng.svg" class="photo" :style="'display:'+finishDisplay1+';'" id="finish" @click="change('writePost','Change')">
        <img src="../../assets/images/wancheng.svg" class="photo" :style="'display:'+finishDisplay2+';'" id="finish" @click="change('ChangeInformation','Change')">
        <img src="../../assets/images/wancheng.svg" class="photo" :style="'display:'+finishDisplay3+';'" id="finish" @click="change('Logon','Change')">
        <img src="../../assets/images/jubao.svg" class="photo" :style="'display:'+complainDisplay+';'" id="complain" >
    </div>
</template>
<script>
export default {
    name:'Title',
    props:{
        title:String,
        back:String,
        write:String,
        writeDisplay:String,
        cutDisplay:String,
        finishDisplay1:String,
        finishDisplay2:String,
        finishDisplay3:String,
        complainDisplay:String
    },
    methods:{
        backf:function(){
            this.$router.go(-1);
        },
        to:function(location){
            this.$router.push({path:location});
        },
        change:function(biggest,small){
            document.getElementById(biggest).style.opacity="50%";
            document.getElementById(biggest).style.zIndex="1";
            document.getElementById(small).style.display="flex";
        }
    }
}
</script>
<style scoped>
#Title{
    position: relative;
    height:40pt;
    display:flex;
    justify-content: space-between;
    align-items:center;
    border-bottom: 1px #B3ACC1 solid;
    background-color:white;
    font-size:15pt;
}
.photo
{
    width:20pt;
    height:20pt;
}
#back
{
    padding-left:10pt;
}
#write,#cut,#finish,#complain
{
    padding-right:10pt;
}
</style>