<template>
  <div class="Location">
    <div id="smallTitle">
      <p style="font-size: 15pt">{{ location }}</p>
      <p style="color: red">*</p>
    </div>
    <input
      :placeholder="'请输入' + location + choose"
      value=""
      id="input"
      @change="change(location,site)"
      v-model="Location"
    />
  </div>
</template>
<script>
export default {
  name: "Location",
  data() {
    return {
      Location: "",
    };
  },
  props: {
    location: String,
    choose: String,
    site:String
  },
  methods: {
    change:function(location,site){
      if(site=="注册账号"){
        if(location=="昵称"){
            this.$store.state.personalInformation.nickname=this.Location;
        }
        if(location=="QQ"){
            this.$store.state.personalInformation.qq=this.Location;
        }
        if(location=="微信"){
            this.$store.state.personalInformation.wechat=this.Location;
        }
        if(location=="专业"){
            this.$store.state.personalInformation.major=this.Location;
        }
        if(location=="学生卡ID"){
            this.$store.state.personalInformation.id=parseInt(this.Location);
        }
        if(location=="密码"){
            this.$store.state.personalInformation.pwd=this.Location;
        }
      }
      if(site=="创建帖子"){
        if(location=="出发地"){
            this.$store.state.createPost.startLocation=this.Location;
        }
        if(location=="目的地"){
            this.$store.state.createPost.destination=this.Location;
        }
      }
    }
  },
};
</script>
<style scoped>
.Location {
  margin: 5%;
  width: 90%;
}
#smallTitle {
  display: flex;
}
input {
  outline: none;
  border: 0px;
  border-bottom: 1px solid #a49bb5;
  width: 100%;
  font-size: 12pt;
}
</style>